<template>
  <div>
    <!-- 帅选条件 -->
    <div class="formDiv">
      <el-form :inline="true" class="demo-form-inline">
        <!-- 查询条件 -->
     
        <el-form-item label="工站">
          <el-select
            v-model="listQuery.work_code"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option
              v-for="(item, index) in WorkCodeNameList"
              :key="index + ' dialog'"
              :label="item.work_name"
              :value="item.work_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="检验方式">
          <el-select
            v-model="listQuery.check_method"
            placeholder="请选择"
            style="width: 100%"
          >
            <el-option label="抽检" value="抽检"></el-option>
            <el-option label="全检" value="全检"></el-option>
          </el-select>
        </el-form-item>

        <!-- 查询按钮 -->
        <el-form-item style="margin-left: 20px !important">
          <el-button type="primary" size="medium" @click="searchBtn"
            >查询</el-button
          >
          <el-button size="medium" @click="resetForm()">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <el-card class="box-card mt20">
      <!-- <div slot="header" class="clearfix" style="line-height: 40px">
        <span>人员列表</span>
      </div> -->
      <div class="">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item>
            <el-button type="primary" size="medium" plain @click="addBtn"
              ><i class="el-icon-plus"></i> 新增</el-button
            >
          </el-form-item>
        </el-form>
        <ELTable
          :tableCol="tableCol"
          :tableData="tableData"
          :listQuery="listQuery"
          :ispagination="ispagination"
          @currentChange="currentChange"
          @sizeChange="sizeChange"
        >
        </ELTable>
      </div>
    </el-card>

    <!-- 新增物料弹出层 -->
    <el-dialog
      :title="dialogTitle"
      width="1000px"
      :visible.sync="dialogFlag"
      :close-on-click-modal="false"
    >
      <el-form
        class=""
        style="padding: 0 60px 0 20px"
        ref="dialogFormRef"
        :model="dialogForm"
        :rules="dialogFormRules"
        label-width="100px"
      >
        <el-row>
          <p class="dialog-title">基本信息</p>

          <el-row :gutter="20">
            
            <el-col :span="12">
              <el-form-item label="工站" prop="rule">
                <el-select
                  v-model="dialogForm.work_code"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option
                    v-for="(item, index) in WorkCodeNameList"
                    :key="index + ' dialog'"
                    :label="item.work_name"
                    :value="item.work_code"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="检验方式" prop="check_method">
                <el-select
                  v-model="dialogForm.check_method"
                  placeholder="请选择"
                  style="width: 100%"
                >
                  <el-option label="抽检" value="抽检"></el-option>
                  <el-option label="全检" value="全检"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>

          <p class="dialog-title">检验参数</p>
          <el-button type="primary" size="small" plain @click="AddListRow()"
            >添加参数</el-button
          >
          <el-table
            :data="dialogForm.response_datas"
            style="width: 100%"
            class="dialogTable"
            height="240"
          >
            <el-table-column fixed="left" label="参数" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.test_parameter'"
                >
                  <el-input v-model="scope.row.test_parameter"></el-input>
                </el-form-item>
              </template>
            </el-table-column>
            <el-table-column label="参数值" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="
                    'response_datas.' + scope.$index + '.test_parameter_count'
                  "
                >
                  <el-input v-model="scope.row.test_parameter_count"></el-input>
                </el-form-item>
              </template>
            </el-table-column>
            <!-- <el-table-column label="状态" align="center">
              <template slot-scope="scope">
                <el-form-item
                  :prop="'response_datas.' + scope.$index + '.test_status'"
                >
                  <el-input v-model="scope.row.test_status"></el-input>
                </el-form-item>
              </template>
            </el-table-column> -->

            <el-table-column label="操作" width="80" align="center">
              <template slot-scope="scope">
                <el-form-item>
                  <el-button
                    size="small"
                    type="danger"
                    plain
                    @click="handleDelete(scope.$index, scope.row)"
                    >删除</el-button
                  >
                </el-form-item>
              </template>
            </el-table-column>
          </el-table>
        </el-row>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFlag = false">取 消</el-button>
        <el-button type="primary" @click="dialogSave">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import ELTable from "@/components/formTablePage/ElTable";
import { baseManagement } from "@/request/api";
import { mapState, mapActions } from "vuex";

export default {
  name: "CheckList",
  components: {
    ELTable,
  },
  data() {
    return {
      // 筛选
      listQuery: {
        prodect_code: "",
        work_code: "",
        production_code: "",
        check_method: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      listQueryBase: {
        prodect_code: "",
        work_code: "",
        production_code: "",
        check_method: "",
        page: 1,
        page_size: 10,
        total: 0,
      },
      ispagination: true, // 分页是否显示
      // 新增人员弹出层
      dialogTitle: "",
      dialogFlag: false,
      dialogForm: {
        check_code: "",
        prodect_code: "",
        production_code: "",
        check_method: "",
        response_datas: [
          {
            test_code: "",
            test_parameter: "",
            test_parameter_count: "",
            test_status: "",
          },
        ],
      },
      dialogFormBase: {
        check_code: "",
        prodect_code: "",
        production_code: "",
        check_method: "",
        response_datas: [
          {
            test_code: "",
            test_parameter: "",
            test_parameter_count: "",
            test_status: "",
          },
        ],
      },
      dialogFormRules: {
      },

      // 表格列
      tableCol: [
        {
          label: "工站名称",
          field: "work_name",
          align: "center",
        },
        {
          label: "检验方式",
          field: "check_method",
          align: "center",
        },
        {
          label: "操作",
          colType: "solt",
          align: "center",
          option: [
            {
              label: "修改",
              event: this.colEditBtn,
            },
            {
              label: "删除",
              event: this.colDeleteBtn,
            },
          ],
        },
      ],
      tableData: [],
    };
  },
  computed: {
    ...mapState(["ProductCodeNameList","WorkCodeNameList"]),
  },
  methods: {
    ...mapActions(["getLogisticProductCodeName","getLogisticWorkCodeName"]),
    // 查询
    searchBtn() {
      this.listQuery.page = 1;
      this.getLogisticCheckProductDeal(this.listQuery);
    },
    // 重置
    resetForm() {
      this.listQuery = JSON.parse(JSON.stringify(this.listQueryBase));
      this.getLogisticCheckProductDeal(this.listQuery);
    },
    // 当前页面改变时
    currentChange(val) {
      this.listQuery.page = val;
      this.getLogisticCheckProductDeal(this.listQuery);
    },
    // 每页条数发生改变时
    sizeChange(val) {
      this.listQuery.page = 1;
      this.listQuery.page_size = val;
      this.getLogisticCheckProductDeal(this.listQuery);
    },

    // 检验参数 查询
    async getLogisticCheckProductDeal(params) {
      await this.getLogisticWorkCodeName();
      baseManagement.getLogisticCheckProductDeal(params).then((res) => {
        console.log("检验参数 查询", res);
        if (res.code == 0) {
          let tableData = res.data.data;
          tableData.forEach((tableDataItem) => {
            this.WorkCodeNameList.forEach((ProductCodeNameListItem) => {
              if (
                tableDataItem.work_code == ProductCodeNameListItem.work_code
              ) {
                tableDataItem.work_name = ProductCodeNameListItem.work_name;
              }
            });
          });

          this.tableData = tableData;
          this.listQuery.total = res.data.total;
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },


    // 检验参数 新增
    postLogisticCheckProductDeal(params) {
      baseManagement.postLogisticCheckProductDeal(params).then((res) => {
        console.log("检验参数 新增", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticCheckProductDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 检验参数 修改
    colEditBtn(rowData) {
      console.log(rowData);
      this.dialogTitle = "修改检验参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = {
        check_code: rowData.check_code,
        prodect_code: rowData.prodect_code,
        production_code: rowData.production_code,
        work_code: rowData.work_code,
        check_method: rowData.check_method,
        response_datas: rowData.response_datas,
      };
    },

    getLogisticPutCheckProductDeal(params) {
      baseManagement.getLogisticPutCheckProductDeal(params).then((res) => {
        console.log("修改检验参数 修改", res);
        if (res.code == 0) {
          this.dialogFlag = false;
          this.getLogisticCheckProductDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 新增
    addBtn() {
      this.dialogTitle = "新增检验参数";
      this.dialogFlag = true;
      this.$nextTick(() => {
        this.$refs.dialogFormRef.clearValidate();
      });
      this.dialogForm = JSON.parse(JSON.stringify(this.dialogFormBase));
    },
    //点击加号添加一行数据
    AddListRow() {
      this.dialogForm.response_datas.push({
        test_parameter: "",
        test_parameter_count: "",
        test_status: "",
      });
    },
    // 弹出层确定
    dialogSave() {
      this.$refs.dialogFormRef.validate((valid) => {
        if (!valid) {
          this.$message({
            message: "请修改正确的数据格式！",
            type: "warning",
          });
          return;
        } else {
          //验证通过调用保存接口
          if (this.dialogTitle == "新增检验参数") {
            console.log("新增检验参数", this.dialogForm);
            this.postLogisticCheckProductDeal(this.dialogForm);
          } else {
            console.log("修改检验参数", this.dialogForm);
            this.dialogForm.response_datas = JSON.stringify(
              this.dialogForm.response_datas
            );

            this.getLogisticPutCheckProductDeal(this.dialogForm);
          }
        }
      });
    },

    // 检验参数 删除
    getLogisticDeleteCheckProductDeal(params) {
      baseManagement.getLogisticDeleteCheckProductDeal(params).then((res) => {
        console.log("检验参数 删除", res);
        if (res.code == 0) {
          this.getLogisticCheckProductDeal(this.listQuery);
          this.$message({
            message: res.message,
            type: "success",
          });
        } else {
          this.$message({
            message: res.message,
            type: "error",
          });
        }
      });
    },

    // 点击删除当前行
    handleDelete(index, rowData) {
      console.log(index);
      console.log(rowData);
      console.log("rowData.test_coderowData.test_code", rowData.test_code);
      if (rowData.test_code) {
        this.$confirm("确定执行删除操作吗？", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning",
        }).then(() => {
          let params = {
            check_code: rowData.check_code,
            response_datas: JSON.stringify([{ test_code: rowData.test_code }]),
          };
          this.getLogisticDeleteCheckProductDeal(params);
          this.dialogForm.response_datas.splice(index, 1);
        });
      } else {
        this.dialogForm.response_datas.splice(index, 1);
      }
    },
    colDeleteBtn(rowData) {
      this.$confirm("确定执行删除操作吗？", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        let params = {
          check_code: rowData.check_code,
          response_datas: JSON.stringify([]),
        };
        this.getLogisticDeleteCheckProductDeal(params);
      });
    },
  },
  mounted() {
    // 检验参数 查询
    this.getLogisticCheckProductDeal(this.listQuery);
  },
};
</script>
<style lang="scss" scoped>
.dialogTable {
  .el-form-item {
    // margin: 0;
    /deep/ .el-form-item__content {
      margin: 0 !important;
    }
  }
}
</style>